.box {
  border-radius: 15px;
  width: 400px;
  height: 60px;
  margin-top: 10px;
  margin-left: auto;
  margin-right: auto;
  box-shadow: 0 4px 8px #0003;
}

.big-timer-text {
  text-align: center;
  font-size: 48px;
}

.small-timer {
  cursor: pointer;
  background-color: #fff;
  border-radius: 15px;
  width: 400px;
  height: 60px;
  margin-top: 10px;
  margin-left: auto;
  margin-right: auto;
  font-size: 32px;
  transition: transform .25s cubic-bezier(.075, .82, .165, 1), margin 3s cubic-bezier(.075, .82, .165, 1), background-color .5s cubic-bezier(.075, .82, .165, 1);
  display: block;
  box-shadow: 0 4px 8px #0003;
}

.small-timer:hover {
  transform: scale(1.05);
}

@keyframes rotateAnimation {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.rotating {
  margin-top: 180px;
  animation: 2s linear infinite rotateAnimation;
}
